import {
  CheckboxGroup,
  Checkbox,
  Stack,
  Radio,
  RadioGroup,
  Button,
  List,
  ListItem,
  ListIcon,
  Divider,
  Text,
  Alert,
  AlertIcon,
  useToast,
  Image,
  Box,
  Modal,
  ModalOverlay,
  ModalHeader,
  ModalCloseButton,
  ModalBody,
  ModalFooter,
  ModalContent,
  useDisclosure,
  Flex,
  Spacer,
} from '@chakra-ui/react'
import {
  CheckCircleIcon,
  InfoIcon,
  QuestionIcon,
  WarningIcon,
} from '@chakra-ui/icons'
import '../App.css'
import { SearchWord } from './SearchWord'
import { useState } from 'react'
// import jsCookie from 'js-cookie'
import titleImg from '../img/titleImg.png'
export const Setting = ({
  questionList,
  loadData,
  history,
  saveHistory,
  showSettingDetail,
  updateQuestionOrder,
  toggleQuestionRange,
  updateQuestionMode,
  selectQuestionList,
  nextQuestion,
  makeSetting,
  addWordFilter,
  deleteWordFilter,
  updateAllSettings,
  loadHistory,
  loadedSetting,
}) => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  const settingDetail = showSettingDetail()
  const [checkMsg, setCheckMsg] = useState()
  const checkSelection = () => {
    let selectedQuestionList = []
    questionList.forEach((group) => {
      if (settingDetail.questionRange.indexOf(group.groupTag) === -1) {
        console.log('2')
        return
      }
      group.groupContents.forEach((question) => {
        let flag = 0
        if (settingDetail.wordFilter.length === 0) {
          flag = 1
        }
        settingDetail.wordFilter.forEach((word) => {
          if (question.detailInfo && question.detailInfo.indexOf(word) > -1)
            flag = 1
          if (
            question.questionSentence &&
            question.questionSentence.indexOf(word) > -1
          )
            flag = 1
          if (question.answer && question.answer.indexOf(word) > -1) flag = 1
          if (question.commentary && question.commentary.indexOf(word) > -1)
            flag = 1
          if (
            question.choices &&
            question.choices.every((choice) => choice.indexOf(word) === -1) ===
              false
          )
            flag = 1
        })
        if (flag === 0) return
        selectedQuestionList.push(question)
      })
    })
    console.log('selectedQuestionList:')
    console.log(selectedQuestionList)
    if (
      settingDetail.wordFilter.length > 0 &&
      selectedQuestionList.length > 0
    ) {
      setCheckMsg('' + selectedQuestionList.length + '')
    } else if (selectedQuestionList.length === 0) {
      setCheckMsg('')
    } else {
      setCheckMsg()
      console.log('1')
    }
  }
  // Cookie

  // if (jsCookie.get('questionOrder')) {
  //   savedSettingDetail.questionOrder = jsCookie.get('questionOrder')
  // }

  // if (jsCookie.get('questionRange')) {
  //   savedSettingDetail.questionRange = jsCookie.get('questionRange').split(',')
  // }
  // if (jsCookie.get('wordFilter')) {
  //   savedSettingDetail.wordFilter = jsCookie.get('wordFilter').split(',')
  // }
  // if (jsCookie.get('history')) {
  //   remainingNum = jsCookie.get('history').split(',').length - 1
  //   console.log(jsCookie.get('history').split(','))
  // }
  return (
    <>
      <Box
        maxW="lg"
        minH={'150px'}
        transitionDelay="3s"
        className="Headline1"
        mb={6}
      >
        <Flex pr={4}>
          <Spacer />
          <Button
            onClick={onOpen}
            colorScheme="blackAlpha"
            variant={'solid'}
            // borderWidth="2px"
            // borderColor="white"
            m={0}
            w={'40px'}
            h="40px"
            mt={4}
            mb={'-80px'}
          >
            <InfoIcon />
          </Button>
        </Flex>

        <Image
          src={titleImg}
          alt="TitleImage"
          m={0}
          p="1"
          mb={-14}
          borderRadius="lg"
        />
        <Flex ml={4} mr="4">
          {checkMsg === '' ? (
            <Button
              colorScheme="teal"
              variant="variant"
              // borderRadius={'full'}
              borderWidth="2px"
              borderColor="white"
              isDisabled
            >
              
            </Button>
          ) : (
            <Button
              bgColor={'blackAlpha.600'}
              color={'white'}
              // borderRadius={'full'}
              borderWidth="2px"
              borderColor="whiteAlpha"
              variant="solid"
              onClick={() => {
                updateQuestionMode('training')
                selectQuestionList(questionList, settingDetail)
                nextQuestion(settingDetail)
                makeSetting()
                saveHistory(history[history.length - 1], settingDetail)
              }}
            >
              
            </Button>
          )}
          <Spacer />
          {loadData &&
          loadData !== {} &&
          loadData.history &&
          loadData.status &&
          loadData.history.split(',').length > 1 ? (
            <Button
              bgGradient="linear(to bottom right, green.300, green.800)"
              color={'white'}
              variant="solid"
              // borderRadius={'full'}
              borderWidth="2px"
              borderColor="whiteAlpha"
              opacity={'0.9'}
              onClick={() => {
                loadHistory(loadData.history, questionList)
                updateAllSettings(loadData.status)
                nextQuestion(loadData.status)
                makeSetting()
                saveHistory(history[history.length - 1], loadData.status)
                // setting
                setTimeout(() => {
                  updateAllSettings(loadData.status)
                }, 2000)
              }}
            >
              ({loadData.history.split(',').length - 1})
            </Button>
          ) : (
            <Button
              bgGradient="linear(to bottom right, green.300, green.800)"
              color={'white'}
              variant="solid"
              isDisabled
              // borderRadius={'full'}
            >
              
            </Button>
          )}
        </Flex>
      </Box>

      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent bgColor={'green.50'}>
          <ModalHeader>TIPS</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <List spacing={3} p={0} bgColor="green.50" fontSize={'sm'}>
              <ListItem>
                <ListIcon as={CheckCircleIcon} color="green.500" />
                PC
              </ListItem>
              <ListItem>
                <ListIcon as={CheckCircleIcon} color="green.500" />
                
              </ListItem>
              <ListItem>
                <ListIcon as={CheckCircleIcon} color="green.500" />
                
              </ListItem>
              <ListItem>
                <ListIcon as={WarningIcon} color="green.500" />
                
              </ListItem>
              <ListItem>
                <ListIcon as={QuestionIcon} color="green.500" />
                
              </ListItem>
            </List>
            <Divider orientation="horizontal" mt={3} mb="1" />
            <Text></Text>
            <Text fontSize={'sm'}>
              09-08_Ver1.2-
            </Text>
            <Text fontSize={'sm'}>09-07_Ver1.1-WebStorage</Text>
            <Text fontSize={'sm'}>
              08-01_Ver1.0-.
            </Text>
            <Text fontSize={'sm'}>07-10_Ver0.9-</Text>
            <Text fontSize={'sm'}>06-01_Ver0.8-</Text>
            <Text fontSize={'sm'}>05-25_Ver0.7-</Text>
            <Text fontSize={'sm'}>
              05-22_Ver0.6-cookie
            </Text>
            <Text fontSize={'sm'}>
              05-09_Ver0.4-
            </Text>
            <Text fontSize={'sm'}>2022-05-05_Ver0.1-</Text>
          </ModalBody>

          <ModalFooter>
            <Button colorScheme="blue" mr={3} onClick={onClose}>
              Close
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
      <Box mr={'auto'} ml="auto" maxW={'sm'}>
        {checkMsg === '' ? (
          <Alert status="error" fontWeight={'semibold'} maxW="lg">
            <AlertIcon />
            {checkMsg}
          </Alert>
        ) : (
          <>
            {checkMsg ? (
              <Alert status="success" maxW={'lg'}>
                <AlertIcon />
                {checkMsg}
              </Alert>
            ) : (
              <></>
            )}
          </>
        )}

        <RadioGroup defaultValue={settingDetail.questionOrder}>
          <Stack spacing={5} direction="row" p={2}>
            <Radio
              colorScheme="red"
              value="random"
              onChange={() => {
                updateQuestionOrder('random')
                // saveSetting(settingDetail)
              }}
            >
              
            </Radio>
            <Radio
              colorScheme="green"
              value="ascend"
              onChange={() => {
                updateQuestionOrder('ascend')
                // saveSetting(settingDetail)
              }}
            >
              
            </Radio>
          </Stack>
        </RadioGroup>
        <CheckboxGroup
          colorScheme="green"
          defaultValue={settingDetail.questionRange}
        >
          <Stack
            w={'sm'}
            spacing={[2, 4]}
            direction={['column']}
            bg="blackAlpha.100"
            p={2}
            mb="5"
          >
            {questionList.map((group, index) => (
              <Checkbox
                value={group.groupTag}
                key={index}
                onChange={() => {
                  toggleQuestionRange(group.groupTag)
                  checkSelection()
                  // saveSetting(settingDetail)
                }}
              >
                {group.groupTag}(
                {group.groupContents ? group.groupContents.length : '0'})
              </Checkbox>
            ))}
          </Stack>
        </CheckboxGroup>
        <SearchWord
          showSettingDetail={showSettingDetail}
          addWordFilter={addWordFilter}
          deleteWordFilter={deleteWordFilter}
          questionList={questionList}
          checkSelection={checkSelection}
          // saveSetting={saveSetting}
        />
      </Box>
      <Divider orientation="horizontal" maxW={'lg'} />
      <Text fontSize="xs" textColor={'blackAlpha.500'} ml="4">
        2022- IgaTatApps
      </Text>
    </>
  )
}
